import { TableItem } from './table-data';
export interface Category {
  priceArray: string[];
  categoryArray: string[];
  brandArray: string[];
}
export class TreeDataGenerator {
  constructor(private TABLE_DATA: TableItem[]) {}
  buildCategories(): Category {
    let pricesArray: number[] = [];
    let priceArray: string[] = [];
    let categoryArray: string[] = [];
    let brandArray: string[] = [];
    this.TABLE_DATA.forEach((object) => {
      pricesArray.push(object.price);
    });
    let maxVal = Math.max(...pricesArray);
    let minVal = Math.min(...pricesArray);
    let x = minVal;
    let ranges = [];
    let range = (maxVal - minVal) / 25;
    let it = 1;
    ranges.push(Math.round(minVal));
    while (x < maxVal) {
      ranges.push(Math.ceil((x + range) / 25) * 25);
      x = x + range;
    }
    for (let i = 0; i < ranges.length - 1; i++)
      priceArray.push(`$${ranges[i]} to $${ranges[i + 1]}`);

    this.TABLE_DATA.forEach((object) => {
      brandArray.push(object.brand);
    });
    this.TABLE_DATA.forEach((object) => {
      categoryArray.push(object.category);
    });
    categoryArray = Array.from(
      new Set(categoryArray)
    ).sort((value1: string, value2: string) => value1.localeCompare(value2));
    brandArray = Array.from(
      new Set(brandArray)
    ).sort((value1: string, value2: string) => value1.localeCompare(value2));
    return { priceArray, categoryArray, brandArray };
  }
  get getTreeData() {
    const { priceArray, categoryArray, brandArray } = this.buildCategories();
    const TREE_DATA = {
      'Sort by Price': {
        ...priceArray,
      },
      'Sort by Brand': {
        ...brandArray,
      },
      'Sort by Availability': {
        0: 'Available',
        1: 'Out of Stock',
      },
      'Sort by Category': {
        ...categoryArray,
      },
    };
    return TREE_DATA;
  }
}
