import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { EXAMPLE_DATA, TableItem } from '../database/table-data';
import { TreeDataGenerator } from '../database/tree-data';
/** File node data with possible child nodes. */
export class FileNode {
  name: string;
  children: FileNode[];
}

/**
 * Flattened tree node that has been created from a FileNode through the flattener. Flattened
 * nodes include level index and whether they can be expanded or not.
 */
export class FlatTreeNode {
  name: string;
  level: number;
  expandable: boolean;
}
@Injectable({
  providedIn: 'root',
})
export class DatabaseService {
  dataChange = new BehaviorSubject<FileNode[]>([]);
  checklistSelectionChange: BehaviorSubject<{
    [filter: string]: string[];
  }> = new BehaviorSubject<{ [filter: string]: string[] }>({});
  rowsToDisplay: BehaviorSubject<number> = new BehaviorSubject<number>(100);
  isLoadingListener: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(
    false
  );
  get data(): FileNode[] {
    return this.dataChange.value;
  }
  get tableData(): TableItem[] {
    this.isLoadingListener.next(true);
    const timeout = this.randomWithRange(500, 1000);
    const TABLE_DATA = EXAMPLE_DATA.slice(0, this.rowsToDisplay.getValue());
    const treeData = new TreeDataGenerator(TABLE_DATA);
    this.initialize(treeData.getTreeData);
    new Promise((resolve) =>
      setTimeout(() => resolve(this.isLoadingListener.next(false)), timeout)
    );
    return TABLE_DATA;
  }
  constructor() {}
  randomWithRange = (min: number, max: number) =>
    Math.random() * (max - min) + min;
  initialize(TREE_DATA) {
    // Build the tree nodes from Json object. The result is a list of FileNodes with nested
    //file node as children.
    const data = this.buildFileTree(TREE_DATA, 0);

    // Notify the change.
    this.dataChange.next(data);
  }

  /**
   * Build the file structure tree. The `value` is the Json object, or a sub-tree of a Json object.
   * The return value is the list of `FileNode`.
   */
  buildFileTree(obj: { [key: string]: any }, level: number): FileNode[] {
    return Object.keys(obj).reduce<FileNode[]>((accumulator, key) => {
      const value = obj[key];
      const node = new FileNode();
      node.name = key;
      if (value != null) {
        if (typeof value === 'object') {
          node.children = this.buildFileTree(value, level + 1);
        } else {
          node.name = value;
        }
      }

      return accumulator.concat(node);
    }, []);
  }
}
