import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { DialogComponent } from './dialog/dialog.component';
import { DatabaseService } from './services/database.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit {
  deselectFilters() {
    this._database.checklistSelectionChange.next({});
  }
  numberToLoad: number;
  constructor(private _database: DatabaseService, public dialog: MatDialog) {}
  isLoading: boolean = false;

  openDialog(): void {
    const dialogRef = this.dialog.open(DialogComponent, {
      width: '250px',
      data: { numberToLoad: this.numberToLoad },
    });
    dialogRef.afterClosed().subscribe((result) => {
      this._database.rowsToDisplay.next(result);
    });
  }
  ngOnInit(): void {
    this._database.isLoadingListener.subscribe(
      (isLoading: boolean) => (this.isLoading = isLoading)
    );
  }

  title = 'online-store';
}
