import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import { flatten } from '@angular/compiler';
import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort, MatSortable } from '@angular/material/sort';
import { MatTable } from '@angular/material/table';
import { DatabaseService, FlatTreeNode } from '../services/database.service';
import { TableItem } from '../database/table-data';
import { TableDataSource } from './table-datasource';

@Component({
  selector: 'app-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.scss'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({ height: '0px', minHeight: '0' })),
      state('expanded', style({ height: '*' })),
      transition(
        'expanded <=> collapsed',
        animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')
      ),
    ]),
  ],
})
export class TableComponent implements AfterViewInit, OnInit {
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatTable) table: MatTable<TableItem>;
  dataSource: TableDataSource;
  filterCategories: string[] = [];
  filteredData: TableItem[] = [];
  originalDataSource: TableItem[];
  isLoading: boolean = false;
  expandedItem: TableItem | null;
  counter: number = 0;
  //checklistSelectionList: FlatTreeNode[] = [];
  constructor(private _database: DatabaseService) {}
  /** Columns displayed in the table. Columns IDs can be added, removed, or reordered. */
  displayedColumns = [
    'key',
    'name',
    'price',
    'brand',
    'category',
    'availability',
  ];

  ngOnInit() {}

  ngAfterViewInit() {
    this._database.rowsToDisplay.subscribe((rowsToDisplay: number) => {
      this.originalDataSource = this._database.tableData;
      this.dataSource = new TableDataSource(this.originalDataSource);
      this.sort.sortChange.subscribe(() => (this.paginator.pageIndex = 0));
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      this.table.dataSource = this.dataSource;
    });
    this._database.checklistSelectionChange.subscribe(
      async (filterObject: { [filter: string]: string[] }) => {
        this._database.isLoadingListener.next(true);
        const timeout = this._database.randomWithRange(100, 700);
        const filteredData: TableItem[] = this.filterReducer(filterObject);
        await new Promise((resolve) =>
          setTimeout(
            () => resolve(this._database.isLoadingListener.next(false)),
            timeout
          )
        );
        const keysOfFilterObject: string[] = Object.keys(filterObject);
        const latestFiltering: string =
          keysOfFilterObject[keysOfFilterObject.length - 1];
        this.dataSource = new TableDataSource(filteredData);

        latestFiltering !== undefined
          ? this.sort.sort(this.sort.sortables.get(latestFiltering))
          : null;
        this.paginator.firstPage();
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        this.table.dataSource = this.dataSource;
        this.table.renderRows();
      }
    );
  }
  filterReducer(filterObject: {
    [filter: string]: string[];
  }): null | TableItem[] {
    let fullResultArray: TableItem[] = this.originalDataSource;
    Object.entries(filterObject).forEach((filterArray: [string, string[]]) => {
      console.log(filterArray);
      let [filterCategory, filters]: [string, string[]] = filterArray;
      switch (filterCategory) {
        case 'price':
          fullResultArray = this.filterByPrice(filters, fullResultArray);
          break;
        case 'brand':
          fullResultArray = this.filterByBrand(filters, fullResultArray);
          break;
        case 'category':
          fullResultArray = this.filterByCategory(filters, fullResultArray);
          break;
        case 'availability':
          fullResultArray = this.filterByAvailability(filters, fullResultArray);
          break;
      }
    });

    return fullResultArray;
  }
  filterByPrice(
    filters: string[],
    dataSource: TableItem[]
  ): TableItem[] | null {
    const flatNumbers = flatten(
      filters.map((filter: string) => filter.match(/\d{1,4}/g))
    ).map((stringValue: string) => +stringValue);
    const maxValue = Math.max(...flatNumbers);
    const minValue = Math.min(...flatNumbers);
    return dataSource.filter(
      (tableItem: TableItem) =>
        tableItem.price > minValue && tableItem.price < maxValue
    );
  }
  filterByBrand(
    filters: string[],
    dataSource: TableItem[]
  ): TableItem[] | null {
    return flatten(
      dataSource.filter((tableItem: TableItem) =>
        filters.includes(tableItem.brand)
      )
    );
  }

  filterByCategory(
    filters: string[],
    dataSource: TableItem[]
  ): TableItem[] | null {
    return flatten(
      dataSource.filter((tableItem: TableItem) =>
        filters.includes(tableItem.category)
      )
    );
  }
  filterByAvailability(
    filters: string[],
    dataSource: TableItem[]
  ): TableItem[] | null {
    return filters.length === 1
      ? filters[0] === 'Available'
        ? flatten(
            dataSource.filter(
              (tableItem: TableItem) => tableItem.availability === true
            )
          )
        : flatten(
            dataSource.filter(
              (tableItem: TableItem) => tableItem.availability === false
            )
          )
      : this.originalDataSource;
  }
  intersection = (tableItems: TableItem[][]) =>
    tableItems.reduce((a: TableItem[], b: TableItem[]) =>
      a.filter((ele) => b.includes(ele))
    );
}
